export default{
	data(){
		return{
			id:null,
			details:{},
			content:""
		}
	},
	onLoad(e){
		this.id=e.id
	},
	onShow(){
		this.getMsgDetails()
	},
	methods:{
		getMsgDetails(){
			this.$http.post("sMessage/getLeaveApplyDetail",{
				id:this.id
			}).then(res=>{
				if(res.code==100){
					this.details=res.info
					if(this.details.newsVoReply){
						this.content=this.details.newsVoReply.content
					}else{
						this.content=""
					}
				}
			})
		},
		goBack(){
			uni.navigateBack({
				
			})
		}
		
	}
}
